import React from 'react';
import { Form, TextArea, Label } from 'semantic-ui-react';
import 'semantic-ui-css/semantic.min.css'
import './App.css';

function App() {

  const [text, setText] = React.useState('');

  function getByteLength(s,b,i,c){
    for(b=i=0;c=s.charCodeAt(i++);b+=c>>11?2:c>>7?2:1);
    return b;
  }
  
  function onChange(e) {
    setText(e.target.value);
  }

  return (
    <div className="App">
      <div className="background"/>
      <Form className="input">
        <Form.Field style={{width: '100%', height: '100%'}}>
        <TextArea style={{width: '100%', minHeight: '100%', maxHeight: '100%'}} onChange={onChange} placeholder='글자를 입력하세요.'/>
        <div>
        <Label style={{boxShadow: '2px 2px 5px 1px #1b1b1b7a'}} color="blue" pointing>글자수: {text.length}</Label>
        <Label style={{boxShadow: '2px 2px 5px 1px #1b1b1b7a'}} color="yellow" pointing='left'>공백 제외: {text.replace(/\s/gi, '').length}</Label>
        </div>
        <div>
        <Label style={{boxShadow: '2px 2px 5px 1px #1b1b1b7a'}} color="blue" pointing>바이트수: {getByteLength(text)}</Label>
        <Label style={{boxShadow: '2px 2px 5px 1px #1b1b1b7a'}} color="yellow" pointing='left'>공백 제외: {getByteLength(text.replace(/\s/gi, ''))}</Label>
        </div>
        </Form.Field>
      </Form>
    </div>
  );
}

export default App;
